<template>
  <div>
    <vSearch
      @getOption="getOption"
      @searchInfo="searchInfo"
      @clearSearchInfo="clearSearchInfo"
      :pageType="pageType"
    ></vSearch>
    <el-table
      :data="tableData"
      style="width: 100%"
      max-height="675"
      :default-sort="{prop: 'date', order: 'descending'}"
    >
      <el-table-column
        label="操作"
        width="60"
      >
        <template slot-scope="scope">
          <el-button
            @click.native.prevent="openDialog(scope.row, 'operate')"
            type="text"
            size="small"
          >
            处理
          </el-button>
        </template>
      </el-table-column>
      <el-table-column
        prop="customerName"
        label="客户名称"
        sortable
      >
      </el-table-column>
      <el-table-column
        prop="customerId"
        label="客户号"
        width="160"
      >
      </el-table-column>
      <el-table-column
        prop="contractId"
        label="合同编号"
        sortable
      >
      </el-table-column>
      <el-table-column
        prop="currentPrice"
        label="当前价格"
        sortable
      >
      </el-table-column>
      <el-table-column
        prop="warningPrice"
        label="预警价格"
        sortable
      >
      </el-table-column>
      <el-table-column
        prop="pledgeRate"
        label="当前质押率"
        :formatter="formatter"
      >
      </el-table-column>
      <el-table-column
        prop="contactMember"
        label="联系客户"
        sortable
      >
      </el-table-column>
    </el-table>
    <div class="pagination">
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page.sync="currentPage"
        :page-size="pageSize"
        layout="prev, pager, next, jumper"
        :total="totalPage"
      >
      </el-pagination>
    </div>

    <el-dialog
      title="处理"
      :visible.sync="dialogFormVisible"
    >
      <el-tabs type="border-card">
        <el-tab-pane label="价值预警">
          <table class="value-warning">
            <tr>
              <th>放款金额</th>
              <td>2</td>
              <!-- <th>&nbsp;</th>
              <td>&nbsp;</td> -->
            </tr>
            <tr>
              <th>仓单原价值</th>
              <td>4</td>
              <th>仓单现价值</th>
              <td>6</td>
            </tr>
            <tr>
              <th>原保证金</th>
              <td>8</td>
              <th>现保证金</th>
              <td>10</td>
            </tr>
          </table>
        </el-tab-pane>
        <el-tab-pane label="保值方案">
          <div class="program">
            需提醒客户追加1000吨的货物或65040000元保证金。
          </div>
          <el-button
            type="primary"
            class="dialog-button"
          >确定</el-button>
        </el-tab-pane>
        <el-tab-pane label="风险处理">
          <div class="program">
            客户实际追加
            <el-input
              v-model="programForm.weight"
              clearable
              style="width:120px"
            >
            </el-input>
            吨的货物，
            <el-input
              v-model="programForm.ammount"
              clearable
              style="width:120px"
            >
            </el-input>
            元保证金。
          </div>
          <el-button
            type="primary"
            class="dialog-button"
          >确定</el-button>
        </el-tab-pane>
      </el-tabs>
    </el-dialog>
    <el-dialog
      title="保值方案"
      :visible.sync="methodVisible"
    >
      <el-tabs type="border-card">
        <el-tab-pane label="保值方案">
          <div class="program">
            需提醒客户追加
            <el-input
              v-model="programForm.weight"
              clearable
              style="width:150px"
            >
            </el-input>
            吨的货物或
            <el-input
              v-model="programForm.ammount"
              clearable
              style="width:150px"
            >
            </el-input>
            元保证金。
          </div>
          <el-button
            type="primary"
            class="dialog-button"
          >确定</el-button>
        </el-tab-pane>
      </el-tabs>
    </el-dialog>
  </div>
</template>

<script>
import vSearch from "../components/Search.vue";

export default {
  data() {
    return {
      pageType: 2,
      searchForm: {
        option: "",
        inputText: ""
      },
      programForm: {
        weight: "",
        ammount: ""
      },
      currentPage: 1,
      pageSize: 1,
      totalPage: 100,

      dialogFormVisible: false,
      methodVisible: false,

      tableData: [
        {
          customerId: "hz-hzjylxmyyxgs-180926",
          customerName: "杭州久益隆鑫贸易有限公司",
          contractId: "al0260025533",
          currentPrice: "6,596,691.4",
          warningPrice: "123123",
          pledgeRate: "50",
          contactMember: "张三 17826833504"
        }
      ]
    };
  },
  methods: {
    getOption(value, inputText) {
      this.searchForm.option = value;
      this.searchForm.inputText = inputText;
    },
    searchInfo() {
      console.log(this.searchForm.option + "+" + this.searchForm.inputText);
    },
    clearSearchInfo() {
      this.searchForm.option = "";
      this.searchForm.inputText = "";
    },
    openDialog(row, type) {
      if (type == "operate") {
        this.dialogFormVisible = true;
      } else if (type == "method") {
        this.methodVisible = true;
      }
    },
    handleSizeChange(val) {
      console.log(`每页 ${val} 条`);
    },
    handleCurrentChange(val) {
      console.log(`当前页: ${val}`);
    },
    formatter(row, column) {
      return row.pledgeRate + "%";
    }
  },
  components: {
    vSearch
  }
};
</script>

<style>
.pagination {
  text-align: center;
}
.value-warning {
  border-spacing: 0;
  border-collapse: collapse;
}
.value-warning tr {
}
.value-warning tr th {
  width: 150px;
  height: 70px;
  border: 1px solid gray;
}
.value-warning tr td {
  width: 200px;
  height: 70px;
  text-align: center;
  border: 1px solid gray;
}
.program {
  height: 100px;
}
.dialog-button {
  float: right;
}
</style>



